<!-- 장바구니 페이지 -->
<template>
  <div
    v-for="(menu, mIndex) in menus"
    :key="mIndex"
    class="card mb-3"
  >
    <div class="card-body">
      <div class="row">
        <div class="col">
          <p class="title" style="text-align: left">
            {{ menu.fname }}<br />{{ menu.foption }}
          </p>
        </div>
        <!-- 친구 / 금액 읿력 collapse 여는 버튼 -->
        <!-- id 부여해야함-->
        <!-- <div class="col-2">
                <button :id="'button-' + mIndex" class="btn btn-primary" type="button" data-bs-toggle="collapse" :data-bs-target="'#collapse-' + mIndex" aria-expanded="false" :aria-controls="'#collapse-' + mIndex">
                    <svg xmlns="http://www.w3.org/2000/svg" width="23" height="23" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                        <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6"/>
                        <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5"/>
                    </svg>
                </button>
            </div> -->
        <div class="col">
          <button
            @click="rmCard(mIndex)"
            type="button"
            class="btn-close"
            aria-label="Close"
          ></button>
        </div>
      </div>

      <!-- <div class="row"> -->
      <div class="input-group mb-3" style="width: 250px">
        <!-- 수량 버튼 -->
        <button
          class="btn btn-outline-secondary"
          @click="decrement(mIndex)"
          type="button"
        >
          -
        </button>
        <input
          type="number"
          :id="'button-' + mIndex"
          class="form-control"
          :value="menu.fcnt"
          disabled
        />
        <button
          class="btn btn-outline-secondary"
          @click="increment(mIndex)"
          type="button"
        >
          +
        </button>
      </div>
      <p>최종 금액 : {{ menu.ftotal }}</p>
      <!-- </div>div.row -->

      <div class="collapse" :id="'collapse-' + mIndex">
        <!-- 친구 / 금액 읿력 collapse, 장바구니속 메뉴들, 카드 인덱스 번호,  -->
        <C_CLcardOpt :menusProp="menus" :mIndex="mIndex" />
      </div>
    </div>
  </div>
  <!--card-->
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
const cardStore = "cardStore";
export default {
  computed: {
    ...mapGetters(cardStore, ["GE_CART_LIST"]),
    menus: function () {
      return this.GE_CART_LIST.map((item) => ({
        fname: item.fname,
        foption: item.foption,
        ftotal: item.ftotal,
        data: item.friends,
        fcnt: item.fcnt,
      }));
    },
  },
  methods: {
    ...mapMutations(cardStore, [
      "INCREMENT_COUNT",
      "DECREMENT_COUNT",
      "RM_CART_LIST",
    ]),
    increment(mIndex) {
      this.INCREMENT_COUNT(mIndex);
    },
    decrement(mIndex) {
      if (this.menus[mIndex].fcnt > 1) {
        this.DECREMENT_COUNT(mIndex);
      }
    },
    rmCard(mIndex) {
      this.RM_CART_LIST(mIndex);
    },
  },
};
</script>